.bg_slide__white {
    background-color: $bgwhite;
}
.bg_slide__viol {
    background-color: $bgviol;
}

.index_head {
    display: block;
    margin-top: 10%;
    font-family: $CRInf;
    font-style: normal;
    font-weight: 600;
    font-size: 96px;
    line-height: 116px;
}

.index_head__menu{
    display: flex;
    align-items: flex-end;
}

.index_head__item{
    font-family: $CRGA;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    list-style: none;
    margin-left: 10px;
    width: 81px;
    text-align: center;
}

a.nav{
    text-decoration: none;
    color: #000000;
}

a.nav:hover{
    color: #d15fff;
}

.index_head__item:hover{
    font-style: italic;
}

header{
    position: fixed;
    display: flex;
    width: 100%;
    padding-left: 40px;
    padding-right:40px;
    padding-top: 10px;
    align-items: center;
    justify-content: space-between;
    z-index: 2;
}