.slide5_title {
    font-family: $CRInf;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    padding-top: 10%;
    line-height: 87px;
    color: $textwhite;
}
.slide5__flex__vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.slide5__flex__horizontal {
    margin-top: 5%;
    display: flex;
    width: 90%;
    justify-content: space-between;
}
.slide5_cost1, .slide5_cost2, .slide5_cost3 {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px; 
    color: $textwhite;
    width: 30%;
}
.slide5_cost1 {
    text-align: left;
}
.slide5_cost2 {
    text-align: center;
}
.slide5_cost3 {
    text-align: right;
}
.slide5_item__fh {
    display: flex;
    flex-direction: row;
    width: 300%;
    justify-content: space-around;
    overflow: hidden;
}
.slide5_item1, .slide5_item2, .slide5_item3 {
    display: flex;
    flex-direction: column;
    justify-items: center;
}
.slide5_item1_logo, .slide5_item2_logo, .slide5_item3_logo {
    object-fit: contain;
    width: 240px;
    height: 240px;
}
.slide5_item1_price, .slide5_item2_price, .slide5_item3_price {
    font-family: $CRInf;
    font-style: normal;
    font-weight: normal;
    font-size: 76px;
    line-height: 76px;
    text-align: center;
    color: $textwhite;

}
.slide5_item1_text, .slide5_item2_text, .slide5_item3_text {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    color: $textwhite;
}
