body {
  font-family: $CRGA;
  overflow: hidden;
  & .title {
    color: #000;
  }
}
p {
  margin-block-start: 0em;
  margin-block-end: 0em;
}

button{
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  outline: inherit;
  cursor: pointer;
}
