.slide6_title {
    font-family: $CRInf;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    padding-top: 6%;
    line-height: 87px;
    color: $textwhite;
}
.slide6_textbox {
    width: 80%;
    margin-top: 5%;
}
.slide6_textbox_text {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    color: $textwhite;
    padding-bottom: 29px;
    :last-child {
        padding-bottom: 0px;
    }
}
.slide6__flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}