.slide7_title {
    font-family: $CRInf;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    padding-top: 10%;
    line-height: 87px;
    color: $textwhite;
}
.slide7__flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.slide7_textbox {
    display: flex;
    flex-direction: column;
    align-items: center;  
    width: 80%;
    margin-top: 4%;
    margin-bottom: 2%;
}
.slide7_textbox_text {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 29px;
    color: $textwhite;
    width: 50%;
    text-align: left;
}
.slide7_textbox_form {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    color: $textblack;
    background-color: $bgwhite;
    width: 50%;
    text-align: center;
    margin-bottom: 3%;
    box-shadow: none;
    border:none;
}
.slide7_textbox_button {
    font-family: "Cormorant Garamond", sans-serif;
    font-style: normal;
    font-weight: 100;
    font-size: 16px;
    line-height: 44px;
    letter-spacing: 1.5px;
    color: #F8F8F8;
    background-color: #3E0043;
    border: 0.25px solid #E5E5E5;
    width: 15%;
}
