.slide_header__viol {
    font-family: $CRInf;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 77px;
    padding-left: 59px;
    position: relative;
    top: 0px;
    color: $textwhite;
}
.slide4_title {
    font-family: $CRInf;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    padding-top: 10%;
    line-height: 87px;
    color: $textwhite;
}
.slide4_flex__vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
}
// .slide4_flex__horizontal {
//     display: flex;
//     flex-direction: row;
//     justify-content: space-between;
// }
.slide4_text {
    width: 50%;
    font-family: $CRGA;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 154%;
    color: $textblack;
    background-color: $bgwhite;
    position: absolute;
    z-index: 1;
    left: 0px;
    margin-top: 5%;
    padding: 5% 25% 5% 5%;
    :last-child {
        margin-bottom: 0px;
    }
    border-radius: 0 10% 0 0;
}
.slide4_text__margin {
    margin-bottom: 28px;
}
.slide4_me {
    position: absolute;
    margin-top: 2%;
    z-index: 2;
    right: 10%;
    width: 30%;
    max-width: 100%;
}